.breadCrumb{
  background: #EFF5F8;
  padding: 13px 0;
  margin-bottom: 95px;
}
.container {
  max-width: 1200px;
  margin: auto;
}

.breadcrumb{
  text-align: center;
}

.breadcrumb_item{
  text-decoration: none;
  color: #949EA5;
  font-size: 15px;
  text-transform: capitalize;
  line-height: 1;
  font-weight: 400;
  &:first-child:after{
    content: '>';
    color: #949EA5;
    margin: 0 10px 0 10px;
  }
  //&:last-child:before{
  //  content: '>';
  //  color: #949EA5;
  //  margin: 0 10px 0 10px;
  //}
  &:hover{
    color: #9F78FF;
  }
}

h2{
  font-size: 24px;
  margin-bottom: 15px;
  font-weight: 600;
  color: #2d2d2d;
}

.home_content_wrap{
  display: flex;
  justify-content: center;
}
img{
  width: 100%
}
.container{
  margin: 20px 50px 50px 50px;
}
.slider_wrap{
  position: relative;
  overflow: hidden;
}

#posts{
  width: 760px;
  box-sizing: border-box;
}

.subpage_name{
  margin-right: 50px;
  margin-left: 50px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: #9F78FF;
  text-align: left;
  padding-bottom: 8px;
  border-bottom: 1px solid #9F78FF;
  margin-bottom: 20px;
}
.post_img_wrap{
  position: relative;
}

.post_body_wrap{
  padding: 50px 30px 35px 35px;
  box-shadow: 0px 10px 20px 0px rgba(221,221,221,0.3);
  margin-bottom: 50px;

  a{
    text-decoration: none;
    &:hover{
      h2{
        color: #9F78FF;
      }
    }
    h2{
      font-size: 24px;
      margin-bottom: 15px;
      color: #2d2d2d;
      font-weight: 600;
    }
  }
}
.post_info{
  display: flex;
  padding: 0;
  margin-top: 30px;

  li:first-child:after{
    content: "|";
    padding-left: 10px;
    padding-right: 10px;
  }
  li{
    list-style: none;
    a{
      color: #999999;
      font-size: 14px;
    }
  }
}



#sidebar{
  margin-right: 50px;
  width: 368px;
  margin-bottom: 50px;
  box-sizing: border-box;
}
.subpage_name2{
  margin: 0;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: #9F78FF;
  text-align: left;
  padding-bottom: 8px;
  border-bottom: 1px solid #9F78FF;
  margin-bottom: 20px;
}
.post_wrap{
  display: flex;

  .little_img{
    width: 100px;
    height: 80px;
    a{
      img{
        height: 100%;
      }
    }
  }

  .little_tost_content{
    padding-left: 20px;
    a{
      text-decoration: none;
      transition: .4s;

      &:hover{
        h3{
          color: #9F78FF;
        }
      }
      h3{
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 6px;
        color: #2d2d2d;
        transition: all 0.3s linear;
      }
    }
  }
}

.blog_author{
  padding: 20px 30px;
  background: #fbf9ff;
  display: flex;
  align-items: center;
  margin: 50px 50px 50px 50px;
  img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 30px;
  }
}
.blog_author_content{

  h4{
    font-size: 14px;
  }
  p{
    font-size: 14px;
  }
}



@media screen and (max-width: 992px){
  .home_content_wrap{
    flex-direction: column;
  }
  #posts{
    margin: auto;
    width: 100%;
  }
  #sidebar{
    width: 100%;
    padding: 20px 50px 50px 50px;
  }
  .breadCrumb{
    margin-bottom: 20px!important;
  }
}

@media screen and (max-width: 768px) {

}

@media screen and (max-width: 480px){
  h2{
    font-size: 20px;
  }
  p{
    font-size: 14px;
  }
  .breadcrumb_item {
    font-size: 13px;
  }
    #posts{
    padding: 20px 20px 50px 20px;
    margin: auto;
  }
  .container{
    margin: 0;
  }
  #sidebar{
    width: 100%;
    padding: 20px 20px 50px 20px;
  }
  .subpage_name{
    margin-right: 0;
    margin-left: 0;
  }
  .blog_author{
    margin: 30px 0 50px 0;
    img{
      width: 45px;
      height: 45px;
      margin-right: 15px;
    }
  }
  .thumb_img{
      width: 45px!important;
    }
  .post_body_wrap{
    padding: 5px 15px 35px 15px;
  }

}




//--------------------POST COMMENTS
.comments_area{
  background: transparent;
  border-top: 1px solid #eee;
  padding: 45px 0;
  margin: 50px 50px 50px 50px;
  h4{
    margin-bottom: 35px;
    font-size: 18px;
  }
  .comment_list{
    padding-bottom: 48px;
  }
}
.single_comment{
  display: flex;
  justify-content: space-between;
  .user{
    display: flex;
    justify-content: space-between;
    .thumb{
      margin-right: 20px;
      img{
        width: 70px;
        border-radius: 50%;
      }
    }
    .desc{
      p{
        margin-bottom: 10px;
        color: #777777;
        font-size: 15px;
        margin-top: 0;
      }
    }
    .comment_info{
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      .comment_info_author{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h5{
          margin: 0;
        }
        p{
          margin-left: 10px;
          margin-bottom: 0;
        }
      }
      .reply_btn{
        a{
          background-color: transparent;
          color: #888888;
          padding: 5px 18px;
          font-size: 14px;
          display: block;
          font-weight: 400;
          text-decoration: none;
          transition: .4s;
          text-transform: uppercase;
        }
      }
    }
  }
}

.comment_form{
  margin: 50px 0 50px 0;
  border-top: 1px solid #eee;
  padding-top: 45px;
  h4{
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 22px;
  }
}

.form_group{
  margin-bottom: 30px;
  textarea{
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #e5e6e9;
    padding-left: 18px;
    padding-top: 18px;
    font-size: 13px;
    background: transparent;
    &:focus{
      outline: 0;
      box-shadow: none;
    }
  }
  button{
    -webkit-transition: .3s;
    -moz-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    background: #fff;
    display: inline-block;
    padding: 18px 44px;
    font-family: "Cinzel",serif;
    font-size: 14px;
    font-weight: 400;
    border: 1px solid #9F78FF;
    letter-spacing: 3px;
    text-align: center;
    color: #9F78FF;
    text-transform: uppercase;
    cursor: pointer;
    &:hover{
      background: #9F78FF;
      color: #fff !important;
      border: 1px solid #9F78FF;
    }
  }
}
.sm_input{
  display: flex;
  justify-content: space-between;
  .form_wrap_input{
    width: 45%;
    .form_group{
      input{
        width: 100%;
        box-sizing: border-box;
        border: 1px solid #e5e6e9;
        border-radius: 0px;
        height: 48px;
        padding-left: 18px;
        font-size: 13px;
        background: transparent;
        &:focus{
          outline: 0;
          box-shadow: none;
        }
      }
    }
  }
}

.quiz_btn{
  background-color: #9F78FF;
  padding: 10px 0;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .sm_input{
    flex-direction: column;
    .form_wrap_input{
      width: 100%;
    }
  }

}

@media screen and (max-width: 480px){
  .comments_area{
    margin: 30px 0 0 0;
  }

  .comment_form{
    margin: 30px 0 0 0;
  }
  .quiz_btn{
    margin: 25px 0 60px 0;
  }

}
