.NewsLatter_wrap{
  background: #fbf9ff;
  padding: 30px;
  margin-bottom: 30px;
  margin-top: 40px;
  .NewsLatter_wrap_title{
    color: #2d2d2d;
    font-size: 20px;
    margin-bottom: 40px;
    margin-top: 0;
    &:after{
      content: "";
      display: block;
      padding-top: 15px;
      border-bottom: 1px solid #f0e9ff;
    }
  }
}

.form_group{
  margin-bottom: 1rem;
  .form_control{
    height: 50px;
    border-color: #f0e9ff;
    font-size: 13px;
    color: #999999;
    padding-left: 20px;
    border-radius: 0;
    display: block;
    width: 100%;
    line-height: 1.5;
    background-color: #fff;
    background-clip: padding-box;
    box-sizing: border-box;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;

    &:focus{
      border-color: #f0e9ff;
      outline: 0;
      box-shadow: none;
      content: "";
    }
  }
}
.button{
  background: #fff;
  color: #9F78FF !important;
  display: inline-block;
  padding: 18px 44px;
  font-family: "Cinzel",serif;
  font-size: 14px;
  font-weight: 400;
  border: 1px solid #9F78FF;
  letter-spacing: 3px;
  text-align: center;
  color: #9F78FF;
  text-transform: uppercase;
  cursor: pointer;
  transition: .3s;
  width: 100%;
  &:hover{
    background: #9F78FF;
    color: #fff !important;
    border: 1px solid #9F78FF;
  }
}