.advertise_wrap{
  margin: 20px 50px 20px 50px;
  font-size: 14px;
  color: #b3b3b3;
  text-align: end;
}
.button{
  background-color: #fff;
  border: none;
  cursor: pointer;
  font-size: 14px;
  color: #b3b3b3;
  border-bottom: 1px solid silver;
  padding: 0;
  margin-right: 5px;

  &:focus{
    outline: none;
    box-shadow: none;
  }
}

.popup_bg{
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  filter: blur(21px);
  content: " ";
  height: 100vh;
  position: fixed;
  width: 100vw;
  z-index: 9;
  background: rgba(31,31,31,.2);
  backdrop-filter: blur(14px);
}

.modal{
  position: relative;
  z-index: 12;
  margin: auto;
  width: 90%;
  padding: 36px 24px;
  background-color: #FFFFFF;

  a{
    position: absolute;
    top: 5px;
    right: 20px;
    font-size: 35px;
    cursor: pointer;
  }

  span{
    font-size: 24px;
    line-height: 26px;
    font-weight: 700;
    color: #252525;
  }
}

@media screen and (max-width: 420px){
  .advertise_wrap{
    margin: 20px 3px 20px 3px;
    text-align: center;
  }
  .button{
    font-size: 12px;
  }
  .advertise_wrap{
    font-size: 12px;
  }
}