
.breadCrumb{
  background: #EFF5F8;
  padding: 13px 0;
  margin-bottom: 95px;
}
.container {
  max-width: 1200px;
  margin: auto;
}

.breadcrumb{
  text-align: center;
}

.breadcrumb_item{
  text-decoration: none;
  color: #949EA5;
  font-size: 15px;
  text-transform: capitalize;
  line-height: 1;
  font-weight: 400;
  &:first-child:after{
    content: '>';
    color: #949EA5;
    margin: 0 10px 0 10px;
  }
  &:hover{
    color: #9F78FF;
  }
}

.main_content{
  max-width: 1200px;
  margin: auto;
  padding: 0 50px 40px 50px;
}
h1{
  font-size: 70px;
  display: block;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: 4px;
  text-align: center;
}
h3{
   font-size: 30px;
   font-weight: 600;
 }
h4{
  font-size: 20px;
  font-weight: 600;
}
p{
  color: #646D77;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.7;
  padding-top: 0;
}
li{
  color: #646D77;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.7;
}
@media screen and (max-width: 768px){
  h1{
    font-size: 50px;
  }
}
@media screen and (max-width: 480px) {
  .main_content{
    max-width: 1200px;
    margin: auto;
    padding: 0 20px 30px 20px;
  }
  h1{
    font-size: 30px;
    font-weight: 600;
    line-height: 1.2;
  }
  p{
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4;
  }
  h3{
    font-size: 20px;
    font-weight: 600;
  }
  h4{
    font-size: 16px;
    font-weight: 600;
  }
  li{
    font-size: 16px;
  }
  .breadCrumb{
    background: #EFF5F8;
    padding: 13px 0;
    margin-bottom: 50px;
  }
}