footer{
  background: #1D2547;
  padding: 30px 50px 30px 50px;

  nav{
    margin-top: 30px;
    ul{
      display: flex;
      justify-content: space-between;
      padding: 0;
      li{
        list-style: none;

        a{
          color: #C2C5DB;
          font-weight: 300;
          font-size: 14px;
          text-decoration: underline transparent;
          &:hover{
            color: #9F78FF;
            text-decoration: underline;
          }
        }
      }
    }
  }
}

footer img{
  width: 40px;
  margin-right: 10px;
}

footer a{
  text-decoration: none;
  color: #FFFFFF;
  font-size: 30px;
  align-items: center;
  display: flex;
}
.social_link{
  margin-top: 30px;
  display: flex;
  .social_link__wrap{
    display: flex;
    .social_link_wrap__item a{
      color: #f8f8f8;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      font-size: 14px;
      line-height: 40px;
      display: inline-block;
      text-align: center;
      margin-right: 8px;
      border: 1px solid rgba(248, 248, 248, 0.12);
      i{
        -webkit-transition: .4s;
        -moz-transition: .4s;
        -o-transition: .4s;
        transition: .4s;
      }
      &:hover{
        color: #9F78FF;
      }
      &:hover i{
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg);
        -moz-transform: rotateY(180deg);
        -ms-transform: rotateY(180deg);
        -o-transform: rotateY(180deg);
      }
    }
  }
}

.all_right{
  border-top: 1px solid #b7a5c6;
  background: #1D2547;
  padding-top: 20px;
  text-align: center;
  padding-bottom: 20px;

  span{
    text-align: center;
    color: #9f9f9f;
  }
}

@media screen and (max-width: 600px) {
  .logo_footer{
    justify-content: center;
  }
  .social_link{
    justify-content: center;
  }
  .footer_nav{
    flex-wrap: wrap;
    justify-content: center;
      li{
        margin: 10px 5px;
      }
  }
}

@media screen and (max-width: 460px){
  footer{
    padding: 30px 20px 30px 20px;
  }
  footer a{
    font-size: 25px;
  }
}