.quiz_btn{
  position: relative;
  text-align: center;
  margin: 50px 0 70px 0;
}
.quiz_btn a{
  color: #FFFFFF;
  background-color: #9F78FF;
  padding: 10px 0;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  border-radius: 5px;
  transition: .4s;
  &:hover{
    background-color: #a44cff;
  }
}
.italic{
  font-style: italic;
}
.bold{
  font-weight: bold;
}