.title{
  background: #EFF5F8;
  padding: 13px 0;
  margin-bottom: 10px;
  h1{
    margin-top: 0;
  }
}


.home_content_wrap{
  display: flex;
  justify-content: center;
}
img{
  width: 100%
}
.container{
  margin: 40px 50px 50px 50px;
}
.slider_wrap{
  position: relative;
  overflow: hidden;
}
.slider_title{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 6px;
  right: 0;
  text-align: center;
  background-color: #0000006b;

  h1{
    color: #FFFFFF;
    top: 50%;
    transform: translate(0, -50%);
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    font-size: 80px;
  }
}
#posts{
  width: 760px;
  box-sizing: border-box;
}

.subpage_name{
  margin-right: 50px;
  margin-left: 50px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: #9F78FF;
  text-align: left;
  padding-bottom: 8px;
  border-bottom: 1px solid #9F78FF;
  margin-bottom: 20px;
}
.post_img_wrap{
  position: relative;
}
.post_img_date{
  text-align: center;
  position: absolute;
  bottom: -20px;
  left: 40px;
  padding: 13px 30px;
  display: block;
  color: #fff;
  background-color: #9F78FF;
  border-radius: 5px;
  p{
    font-size: 30px;
    font-weight: 600;
    color: #fff;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1.2;
  }
  span{
    font-size: 18px;
    margin-bottom: 0;
    color: #fff;
  }
}
.post_body_wrap{
  padding: 50px 30px 35px 35px;
  box-shadow: 0px 10px 20px 0px rgba(221,221,221,0.3);
  margin-bottom: 50px;

  a{
    text-decoration: none;
    &:hover{
      h2{
        color: #9F78FF;
      }
    }
    h2{
      font-size: 24px;
      margin-bottom: 15px;
      color: #2d2d2d;
      font-weight: 600;
    }
  }
}
.post_info{
  display: flex;
  padding: 0;
  margin-top: 30px;

  li:first-child:after{
    content: "|";
    padding-left: 10px;
    padding-right: 10px;
  }
  li{
    list-style: none;
    a{
      color: #999999;
      font-size: 14px;
    }
  }
}




.btn_loadmore{
  text-align: center;
  button{
    background-color: #FFFFFF;
    border: none;
    cursor: pointer;
    font-size: 20px;
    color: #9F78FF;
    &:hover{
      color: #674da4;
    }
  }
}

@media screen and (max-width: 992px){
  .home_content_wrap{
    flex-direction: column;
  }
  #posts{
    margin: auto;
    width: 100%;
  }
  #sidebar{
    width: 100%;
    padding: 20px 50px 50px 50px;
  }
}

@media screen and (max-width: 768px) {
  .slider_title{

    h1{
      font-size: 40px;
    }
  }
}

@media screen and (max-width: 480px){
  #posts{
    padding: 20px 20px 50px 20px;
    margin: auto;
  }
  .container{
    margin: 0;
  }
  #sidebar{
    width: 100%;
    padding: 20px 20px 50px 20px;
  }
  .subpage_name{
    margin-right: 0;
    margin-left: 0;
  }
  .post_img_date{
    p{
      font-size: 20px;
    }
    span{
      font-size: 14px;
    }
  }
}