
header{
  height: 100px;
  background: #fff;
  padding: 0 30px 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo{
    span{
      font-weight: bold;
      text-transform: uppercase;
      a{
        text-decoration: none;
        font-size: 30px;
      }
    }
  }
  nav{
    ul{
      display: flex;
      padding: 0;
      margin: 0;
      li{
        list-style: none;
        a{
          color: #1D2547;
          font-weight: 600;
          padding: 39px 9px;
          display: block;
          font-size: 15px;
          font-family: "Poppins",sans-serif;
          -webkit-transition: .3s;
          -moz-transition: .3s;
          -o-transition: .3s;
          transition: .3s;
          text-transform: uppercase;
          text-decoration: none;

          &:hover{
            color: #9F78FF;
          }
        }
      }
    }
  }
}
.logo{
  max-width: 300px;
  min-width: 180px;
  margin-top: 10px;
}
.social_link{
  display: flex;
  .social_link__wrap{
    display: flex;
    .social_link_wrap__item a{
      color: #1D2547;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      font-size: 14px;
      line-height: 40px;
      display: inline-block;
      text-align: center;
      margin-right: 8px;
      border: 1px solid rgba(29,37,71,0.12);
      i{
        -webkit-transition: .4s;
        -moz-transition: .4s;
        -o-transition: .4s;
        transition: .4s;
      }
      &:hover{
        color: #9F78FF;
      }
      &:hover i{
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg);
        -moz-transform: rotateY(180deg);
        -ms-transform: rotateY(180deg);
        -o-transform: rotateY(180deg);
      }
    }
  }
}

.navbar{
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
  //position: fixed;
  //left: 0;
  //right: 0;
  //outline: currentcolor none medium;
  //max-width: 100%;
  //margin: 0px;
  //color: rgb(248, 248, 248);
  //min-width: 0px;
  //min-height: 0px;
  //padding: 6px 60px;
  //box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 16px;
  z-index: 500;
  position: absolute;
  left: 0;
  right: 0;
  top: -5px;
  background-color: #fff;
  padding: 0 30px 0 30px;
}
.mobile_menu{
  display: none;
  position: absolute;
  top: -100%;
  width: 95%;
  margin: auto;
  left: 0;
  right: 0;
  background-color: #ffff;
}
.mobile_btn{
  display: none;
}
.mobBtn{
  display: none;
}
@media screen and (max-width: 1000px){
  header {
      height: 35px;
      padding: 30px 20px;
    nav{
      display: none;
    }
  }
  .social_link{
    display: none;
  }
  .navbar{
    padding: 20px 20px;
  }
  .mobBtn {
    display: block;

      &:hover{
        cursor: pointer;
      }

  }
  .mobile_menu{
    display: block;
    top: 95px;
  }

}

